.signin-outer {
  height: 100vh;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-size: cover;
  background-image: url("../../../assets/img/background.jpg");
}

.card-signin {
  border-radius: 40px;
  width: 100%;
  background-repeat: no-repeat;
  background-position: bottom left;
  background-size: 390px;
  box-shadow: 5px 6px 17px 0 rgba(0, 0, 0, 0.11);
  background-color: #ffffffbf;
  padding: 50px 8px !important;
}

.btn {
  border-radius: 7px;
  height: 38px;
  width: 100%;
  background-color: #fd6400;
  box-shadow: none;
}

.card-body {
  margin: 0 auto;
  width: 70%;
}

.select-lang {
  float: right;
  width: 200px;
  margin-top: 16px;
  margin-right: 21px;
}

a {
  color: #3e3e3e;
  font-weight: 500;
}

.signup a {
  color: #407ec9;
}

.w-75 {
  width: 480px !important;
}

.info {
  color: #3e3e3e;
  margin-top: 16px;
  font-size: 14px;
}

.btn1 {
  color: #000;
  background-color: #fff;
  border: solid 0.4px #979797 !important;
  margin: 0 0 0 0;
}

.btn-primary:not(:disabled):not(.disabled):active {
  color: #fff;
  background-color: #fd6400 !important;
  border-color: #fd6400 !important;
}

@media (max-width: 1025px) {

  .card-signin {
    background-size: 390px;
    box-shadow: none;
    background-color: transparent;
    border: none;
  }
}

@media only screen and (max-width: 550px) {
  .card-signin {
    border-radius: 40px;
    width: 100%;
    background-repeat: no-repeat;
    background-position: bottom left;
    background-size: 390px;
    box-shadow: none;
    background-color: transparent;
    border: none;
  }

  .w-75 {
    width: 100% !important;
  }

  .card-body {
    margin: 0 auto;
    width: 90%;
  }
}

.red {
  color: red;
}

.green {
  color: green;
}